import React from 'react'
import Img from 'gatsby-image'
import { graphql, useStaticQuery } from 'gatsby'

export default React.memo(function RandomBorisImage({ number, ...props }) {
  const allImages = useStaticQuery(graphql`
    {
      allFile(filter: { relativeDirectory: { eq: "boris-weird" } }, sort: { fields: childrenImageSharp___fixed___originalName }) {
        nodes {
          childImageSharp {
            fixed(quality: 50, width: 100, height: 100) {
              ...GatsbyImageSharpFixed
            }
          }
        }
      }
    }
  `).allFile.nodes

  const randomImageData = allImages[number].childImageSharp.fixed

  return <Img fixed={randomImageData} {...props} />
})
